import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Input
} from '@angular/core';
import {
  SharedModuleEventHandlerService,
  sharedModuleEventHandlerType
} from '@shared-event-handler';

@Component({
  selector: 'tce-web-lesson-overview',
  templateUrl: './lesson-overview.component.html',
  styleUrls: ['./lesson-overview.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LessonOverviewComponent implements OnInit {
  @Input() public data: any;
  public display = false;
  public keepItOpen = false;
  constructor(
    private sharedModuleEventHandlerService: SharedModuleEventHandlerService
  ) { }

  public ngOnInit(): void {
    console.log(this.data);
    this.alwaysOpenState();
  }

  public toggle() {
    this.display = !this.display;
    this.sharedModuleEventHandlerService.broadcast(
      sharedModuleEventHandlerType.LESSON_OBJECTIVE_TOGGLE,
      !this.display
    );
  }

  public alwaysOpenState(){
    if(this.data.alwaysOpen){
      this.keepItOpen = this.data.alwaysOpen;
      this.display = this.keepItOpen;
    }
  }

}
