import { Component, OnInit, OnDestroy } from '@angular/core';
import { PlayerFacade } from './../../lesson-player-facade';
import { ActivatedRoute } from '@angular/router';
import { takeUntil, map } from 'rxjs/operators';
import { Subject, combineLatest } from 'rxjs';
import { messages } from './../../message-dictionary';

@Component({
  selector: 'nx-afs-lesson-overview-description',
  templateUrl: './lesson-overview-description.component.html',
  styleUrls: ['./lesson-overview-description.component.scss']
})
export class LessonOverviewDescriptionComponent implements OnInit, OnDestroy {
  public destroy$: Subject<boolean> = new Subject<boolean>();
  data;
  playerType;
  modulename = {
    browseBook: true,
    myLesson: false
  };

  constructor(
    private playerFacade: PlayerFacade,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {

    combineLatest(
      this.playerFacade.getTqsData(),
      this.activatedRoute.queryParams
    )
      .pipe(
        map(observer => observer),
        takeUntil(this.destroy$)
      )
      .subscribe(subscribes => {
      console.log("LessonOverviewDescriptionComponent -> ngOnInit -> subscribes", subscribes)
        this.data = {
          alwaysOpen: this.modulename[subscribes[1].module],
          data: subscribes[0],
          title: messages[`overview_${this.playerFacade.getOverViewState()}`]
        }
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
